<template>
  <div class="client-auth">
    <div class="close-btn" @click="closePage">X</div>
    <h3 class="title">手机动态密码登录</h3>
    <div class="tips">未注册的手机号验证后将自动创建新账号</div>
    <div class="phone-bar">
      <select class="country-code">
        <option value="0">+86</option>
      </select>
      <input class="phone-number" type="text" placeholder="手机号">
    </div>
    <pub-btn class="send-code">发送验证码</pub-btn>
  </div>
</template>
<script>
export default {
  // 注册登录
  name: 'Auth',
  data () {
    return {}
  },
  methods: {
    closePage () {
      this.$router.go(-1)
    }
  }
}
</script>
<style lang="less" scoped>
.client-auth {
  padding: 32px 48px;
}
.close-btn {
  margin-bottom: 48px;
}
.title {
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 50px;
  color: @primaryTextColor;
}
.tips {
  margin-bottom: 48px;
  color: @primaryTextColor;
}
.phone-bar {
  display: flex;
  justify-content: space-between;
  color: @primaryTextColor;
  .country-code {
    width: 40%;
    outline: none;
    .ignore-border-1px(#ebebeb);
    .ignore-border-radius(8px);
    &:focus {
      border-color: #008489;
    }
  }
  .phone-number {
    box-sizing: border-box;
    padding: 22px;
    width: 55%;
    height: 98px;
    outline: none;
    .ignore-border-1px(#ebebeb);
    .ignore-border-radius(8px);
    &:focus {
      border-color: #008489;
    }
  }
}
.send-code {
  margin-top: 32px;
}
</style>



